<template>
  <div>
    <div class="selectWrp">
      <span>设备下发情况列表</span>
      <select>
        <optgroup>
          <option value>2020年1月</option>
          <option value>2020年2月</option>
          <option value>2020年3月</option>
          <option value>2020年4月</option>
          <option value>2020年5月</option>
          <option value>2020年6月</option>
        </optgroup>
      </select>
    </div>
    <div id="container"></div>
  </div>
</template>

<script>
import { Line } from "@antv/g2plot";
export default {
  mounted() {
    const data = [
      { year: "2020年1月1日", value: 123 },
      { year: "2020年1月3日", value: 234 },
      { year: "2020年1月7日", value: 433 },
      { year: "2020年1月11日", value: 245 },
      { year: "2020年1月13日", value: 439 },
      { year: "2020年1月15日", value: 622 },
      { year: "2020年1月20日", value: 357 },
      { year: "2020年1月25日", value: 559 },
      { year: "2020年1月30日", value: 633 }
    ];

    const linePlot = new Line(document.getElementById("container"), {
      title: {
        visible: true,
        alignTo:"right",
        text: "设备佩戴数"
      },
      description: {
        visible: false,
        alignTo:"right",
        text: "2020年对应月份不同日期设备佩戴数。"
      },
      forceFit: true,
      padding: "auto",
      data,
      xField: "year",
      yField: "value",
      point: {
        visible: true
      },
      label: {
        visible: true,
        type: "point"
      }
    });

    linePlot.render();
  }
};
</script>

<style scoped>
.selectWrp {
  display: flex;
  justify-content: flex-start;
}
.selectWrp span {
  margin-right: 12px;
}
</style>